import React, {useState, useEffect} from 'react';
import { Formik, useFormik } from 'formik';
import axios from 'axios';
import './Form.css';

const validate = values => {

  const errors = {};
  if (!values.name) {
    errors.name = 'Required';
  } else if (values.name.length > 15) {
    errors.name = 'Must be 15 characters or less';
  }

  if (!values.title) {
    errors.title = 'Required';
  } else if (values.title.length > 50) {
    errors.title = 'Must be 50 characters or less';
  }

  return errors;
};

const Form = ({setReload, values}) => {

  // ensures useEffect on [values] only fires when values are changed ...
  // ... and not on page load.
  const [firstRender, setFirstRender] = useState(true);


  React.useEffect(() => {
    setFirstRender(false);
  }, []);

  // fills in matching fields with fields in row to be editted.
  // then sends the fields as UPDATE request to endpoint to update row that matches id.
  React.useEffect(() => {
    if (!firstRender) {
      // alert("triggered!");
      var obj = JSON.parse(values);
      formik.values.name = obj.name;
      formik.values.title = obj.title;
      formik.values.body = obj.body;
    }
  }, [values]);

  // Pass the useFormik() hook initial form values, a validate function that will be called when
  // form values change or fields are blurred, and a submit function that will
  // be called when the form is submitted
  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      name: '',
      title: '',
      body: '',
    },
    validate,
    onSubmit: async values => {
        // alert(JSON.stringify(values, null, 2));
        const json = JSON.stringify(values, null, 2);
        // console.log(json);
        const res = await axios.post('http://localhost:3000/api/v1/feedbacks/', json, {
          headers: {
            // Overwrite Axios's automatically set Content-Type
            'Content-Type': 'application/json'
          }
        }); 

      formik.values.name = '';
      formik.values.title = '';
      formik.values.body = '';

      setReload(true);
      console.log("reloading...");
      // console.log(res.data);
    },
  });
  return (
  <div className='form__container'>
    <h1>We'd Love to Hear From You!</h1>
    <Formik enableReinitialize>
    <form className="form__body" onSubmit={formik.handleSubmit}>
      <label htmlFor="name">Name <span className="red">*</span></label>
      <input
        id="name"
        name="name"
        type="text"
        onChange={formik.handleChange}
        value={formik.values.name}
      />
      {formik.errors.name ? <div className="error-msg">{formik.errors.name}</div> : null}

      <label htmlFor="title">Title <span className="red">*</span></label>
      <input
        id="title"
        name="title"
        type="text"
        onChange={formik.handleChange}
        value={formik.values.title}
      />
      {formik.errors.title ? <div className="error-msg">{formik.errors.title}</div> : null}

      <label htmlFor="body">Message</label>
      <textarea 
        id="body" 
        name="body" 
        type="text" 
        placeholder="Write something.."
        onChange={formik.handleChange}
        value={formik.values.body} 
      />
      <button className="submit-btn" type="submit">Submit</button>
    </form>
    </Formik>
  </div>
  );
};

export default Form;